// adduse

.dx-map-container,
.dx-map-shield {
  position: relative;
  width: 100%;
  height: 100%;
  color: #000;
}

.dx-map-shield {
  top: -100%;
  left: 0;
  background: rgba(0, 0, 0, 0.01);
  opacity: 0.01;
  pointer-events: all;
}

.dx-map-marker {
  user-select: none;
}

.dx-map-marker-tooltip {
  margin: 10px;
}
